import React from 'react';
import {Link} from 'react-router-dom';







 const Box =({name,image,event}) => {
    
    return(  
    <div className='tc bg-washed-blue w-30 h-20 dib  pa2 ma3 grow   bw2 shadow-5  mr2 mb1 br3 ' >

        <div>
            <h2 className='f1 ' >{name}</h2>

        {image} 
       
       
         
        <div className='f2 w-100 h-30 ba grow shadow-4  tc bg-silver' >
        <Link to= '/app2' style = {{textDecoration: 'none'}}> {event} </Link>
        </div>
        </div>



    </div> 


);}

export default Box;



